<template>
  <div class="bigBox">
    <div id="watchComic">
      <div class="imgBox" ref="imgBox"></div>
      <div class="loveBox" @click="loveClick">
        <svg
          class="svg"
          xmlns="http://www.w3.org/2000/svg"
          :fill="svgFill"
          viewBox="0 0 512 512"
        >
          <path
            d="M47.6 300.4L228.3 469.1c7.5 7 17.4 10.9 27.7 10.9s20.2-3.9 27.7-10.9L464.4 300.4c30.4-28.3 47.6-68 47.6-109.5v-5.8c0-69.9-50.5-129.5-119.4-141C347 36.5 300.6 51.4 268 84L256 96 244 84c-32.6-32.6-79-47.5-124.6-39.9C50.5 55.6 0 115.2 0 185.1v5.8c0 41.5 17.2 81.2 47.6 109.5z"
          />
        </svg>
      </div>
      <div class="showLogin" ref="showLogin" v-if="showLogin"></div>
      <div class="mask" v-if="maskShow" @click="maskClick">
        <div class="QQBox" @click.stop>
          <div class="QRcode">
            <img src="../../assets/img/QQ/16852923665896.png" alt="" />
          </div>
          <div class="QQLogo"></div>
          <div class="text">
            <h4>请加入QQ群获得通行码，输入后即可查阅下一页</h4>
          </div>
          <div class="inputBox">
            <input class="textInput" type="text" v-model="textInput" />
          </div>
          <div class="warringBox" v-show="warringBoxShow">
            <span>通行码错误</span>
          </div>
          <div class="inputClickBox">
            <button @click="inputClick">确定</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getHashCode } from "../../components/xuFunction/getHashCode";
import { Execute } from "@/components/xuFunction/execute";
export default {
  name: "WatchComic",
  components: {},
  data() {
    return {
      svgFill: "#7a7a7b",
      num: 0,
      password: 280403554,
      showLogin: false,
      execute: new Execute(),
      maskShow: false,
      warringBoxShow: false,
      textInput: undefined,
    };
  },
  mounted() {
    let arr = this.$route.params.url.split("_");
    this.url1 = "https://" + "img4.qy0.ru" + "/" + arr[0] + "/";
    this.url2 = arr[1] + "/" + arr[2] + "/";
    this.url3 = "." + arr[3].split(".")[1];
    this.index = Number(arr[3].split(".")[0]) - 1;
    this.imgFristUrl =
      this.url1 + this.url2 + this.decoder1(this.index + 1) + this.url3;
    //判断是否已经喜欢
    this.islove();
    this.gogoPornImg(true);
  },
  beforeUnmount() {
    window.stop();
    window.removeEventListener("scroll", this.getBoxGetImg, true);
  },
  unmounted() {
    this.$emit("unmountedF", "");
  },
  methods: {
    gogoPornImg(boolean) {
      this.index++;
      let imgElement = document.createElement("img"); //创建标签
      imgElement.src =
        this.url1 + this.url2 + this.decoder1(this.index) + this.url3;
      imgElement.style.width = "100%";
      let that = this;
      imgElement.onload = function() {
        if (boolean) {
          that.$refs.imgBox.appendChild(imgElement); //置入标签
          if (that.num < 5) {
            that.gogoPornImg(boolean);
            that.num++;
          } else {
            that.showLogin = true;
            window.addEventListener("scroll", that.getBoxGetImg, true);
          }
        } else {
          that.$refs.imgBox.appendChild(imgElement); //置入标签
          that.gogoPornImg(boolean);
        }
      };
      imgElement.onerror = function() {
        that.index = 0;
      };
    },
    //判斷是否符合密碼
    jurjuPassWord() {
      let localPassword = localStorage.getItem("password");
      let localPasswordString = String(localPassword);
      let passwordHash = getHashCode(localPasswordString);
      if (this.password == passwordHash) {
        this.showLogin = false;
        this.gogoPornImg(false);
        window.removeEventListener("scroll", this.getBoxGetImg, true);
      } else {
        this.maskShow = true;
      }
    },
    inputClick() {
      if (getHashCode(this.textInput) == this.password) {
        localStorage.setItem("password", this.textInput);
        this.showLogin = false;
        this.gogoPornImg(false);
        this.maskClick();
        window.removeEventListener("scroll", this.getBoxGetImg, true);
      } else {
        this.warringBoxShow = true;
      }
    },
    maskClick() {
      this.maskShow = false;
      this.warringBoxShow = false;
      this.textInput = undefined;
    },
    getBoxGetImg() {
      //进入可视区域内后
      let offset = this.$refs["showLogin"].getBoundingClientRect();
      let executeNum = this.execute.differenceComparison(offset.top);
      let offsetTop = offset.top;
      let offsetBottom = offset.bottom;
      // 进入可视区域
      if (offsetTop <= window.innerHeight && offsetBottom >= 0) {
        // 向tableData里的videoUrl直接注入请求地址，就会自动发送请求了
        if (executeNum < 0) {
          if (this.getOddComicSwitch) {
            this.getOddComicSwitch = false;
            this.jurjuPassWord();
          }
        }
      } else {
        if (!this.getOddComicSwitch) {
          this.getOddComicSwitch = true;
        }
      }
    },
    //零零添加器
    add00(index, num) {
      let forI1 = num - String(index).length;
      let strNumb = "";
      for (let i = 0; i < forI1; i++) {
        strNumb = strNumb + "0";
      }
      let str = strNumb + index;
      return str;
    },
    //解码器1
    decoder1(index) {
      return this.add00(index, 2); //翻译出 01 02 ..... 10 11
    },
    loveClick() {
      let myFavorite = JSON.parse(localStorage.getItem("myFavorite"));
      let has = false; //判断有没有重复
      let delIndex;
      for (let i = 0; i < myFavorite.length; i++) {
        if (myFavorite[i] == this.imgFristUrl) {
          has = true;
          delIndex = i;
          break;
        }
      }
      if (!has) {
        myFavorite.splice(0, 0, this.imgFristUrl);
        let parsed = JSON.stringify(myFavorite);
        localStorage.setItem("myFavorite", parsed);
      } else {
        myFavorite.splice(delIndex, 1);
        let parsed = JSON.stringify(myFavorite);
        localStorage.setItem("myFavorite", parsed);
      }

      if (this.svgFill == "#7a7a7b") {
        this.svgFill = "#ff6d6d";
      } else {
        this.svgFill = "#7a7a7b";
      }
    },
    //判断是否已经喜欢
    islove() {
      if (!localStorage.getItem("myFavorite")) {
        let myFavorite = [];
        let parsed = JSON.stringify(myFavorite);
        localStorage.setItem("myFavorite", parsed);
      }
      let myFavorite = JSON.parse(localStorage.getItem("myFavorite"));
      for (let i = 0; i < myFavorite.length; i++) {
        if (myFavorite[i] == this.imgFristUrl) {
          this.svgFill = "#ff6d6d";
          break;
        }
      }
    },
  },
};
</script>

<style scoped>
.bigBox {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
#watchComic {
  display: inline-flex;
  justify-content: center;
  position: relative;
  width: 80%;
}
.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
.QQBox {
  width: 500px;
  background: rgba(151, 151, 151, 0.703);
  border-radius: 5px;
}
.text,
.inputBox,
.inputClickBox,
.warringBox,
.QRcode {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 0;
}
.QRcode img {
  width: 100px;
}
.text,
.inputBox {
  color: #222224;
}
.text {
  padding: 0 10px;
}
.warringBox span {
  color: rgb(255, 56, 56);
}
.inputClickBox button {
  padding: 4px 20px;
  cursor: pointer;
}
.showLogin {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
}
.imgBox {
  position: relative;
}
.loveBox {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 40px;
  height: 40px;
  margin: 0 11% 7px 0;
  cursor: pointer;
  transition: 0.2s;
  z-index: 100;
}

.svg {
  width: 100%;
  height: 100%;
  color: #b0b0b0;
}

@media screen and (max-width: 1000px) {
  #watchComic {
    width: calc(100% - 7px);
    margin-left: 7px;
  }
  .loveBox {
    margin: 0 14px 7px 0;
  }
}
</style>
