//将string转成哈希值的方法
export function getHashCode(str) {
  str = str.toLowerCase();
  var hash = 1315423911,
    i,
    ch;
  for (i = str.length - 1; i >= 0; i--) {
    ch = str.charCodeAt(i);
    hash ^= (hash << 5) + ch + (hash >> 2);
  }
  let hashCode = hash & 0x7fffffff;
  return hashCode;
}
